:root{--primary-color: #3f4257;--secondary-color: #ff5e3a;--primary-text-color: #9a9fbf;--active-text-color: #515365;--danger: #f92552;--success: #08ddc1}body{background-color:#edf2f6}*{margin:0;padding:0;outline:none;font-family:Roboto, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a{text-decoration:none;color:var(--primary-text-color)}ul{list-style:none}.messages__field{width:70%;border-left:1px solid #e6ecf5}.messages__field__title{display:flex;padding:23px 25px 18px;justify-content:space-between;align-items:center;border-bottom:1px solid #e6ecf5}.messages__title{font-size:.875rem;font-weight:700;line-height:1.3;color:var(--active-text-color)}.messages__area{display:flex;height:55%;flex-direction:column;padding:30px 70px;margin:0 auto;overflow-y:scroll}.messages__area ::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:10px;background-color:#F5F5F5}.messages__area::-webkit-scrollbar{width:12px;background-color:#FFF}.messages__area::-webkit-scrollbar-thumb{border-radius:10px;box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#ccc}
